import {Component, ElementRef, EventEmitter, Input, Output, ViewEncapsulation} from '@angular/core';
import {
  AdaptRadarAngularCustomEventName,
  AdaptTranslateService,
  KeyValueObject,
  toNumber,
  TooltipPlacement
} from '@bmc-ux/adapt-angular';
import {RatingLikeEnum} from './rating-like.enum';
import {AdaptRadarGenericDirective, AdaptRadarService} from '@bmc-ux/adapt-radar';

export const RatingLikeTypes = [
  'terrible',
  'disliked',
  'ok',
  'good',
  'excellent'
];

export class RatingLikeValuesError extends Error {
  constructor(length: number) {
    super(`Rating Like values parameter should have 3 or 5 string values. Instead got ${length} values.`);
  }
}

@Component({
  selector: 'adapt-rating-like',
  // eslint-disable-next-line @angular-eslint/no-host-metadata-property
  host: {
    '[class.a-rating]': 'true',
    '[class.a-rating_like]': 'true',
    '[class.a-rating_small]': 'size === "small" ',
    '[class.a-rating_medium]': 'size === "medium" ',
    '[class.a-rating_large]': 'size === "large" '
  },
  templateUrl: './rating-like.component.html',
  encapsulation: ViewEncapsulation.None
})

export class AdaptRatingLikeComponent extends AdaptRadarGenericDirective {
  selectedLike: number = null;
  types: string[] = RatingLikeTypes;

  @Input() disabled: boolean = false;

  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() readonly onChange: EventEmitter<{ type: string }> = new EventEmitter();

  @Input()
  get ratingValues(): string[] {
    return this._ratingValues;
  }

  set ratingValues(v: string[]) {
    if (v.length === 5) {
      this._ratingValues = v;
    } else if (v.length === 3) {
      this._ratingValues = ['', ...v, ''];
    } else {
      throw new RatingLikeValuesError(v.length);
    }
  }

  @Input()
  get point(): RatingLikeEnum {
    return this._point;
  }

  set point(v: RatingLikeEnum) {
    this._point = toNumber(v);
  }

  @Input()
  get tooltip(): string[] {
    return this._tooltip;
  }

  set tooltip(v: string[]) {
    this._tooltip = v;
  }

  @Input()
  get tooltipPlacement(): TooltipPlacement {
    return this._tooltipPlacement;
  }

  set tooltipPlacement(placement: TooltipPlacement) {
    this._tooltipPlacement = placement;
  }

  @Input()
  get size(): 'extra-small' | 'small' | 'medium' | 'large' | 'small' {
    return this._size;
  }

  set size(v: 'extra-small' | 'small' | 'medium' | 'large' | 'small') {
    this._size = v;
  }

  @Input()
  get disabledList(): number[] {
    return this._disabledList;
  }

  set disabledList(array: number[]) {
    this._disabledList = array;
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @Input() set rate(v: any) {
    const setValue = toNumber(v);

    if (setValue && setValue > 0 && setValue <= this.point) {
      this.selectedLike = setValue - 1;
    }
  }

  private _ratingValues: string[] = RatingLikeTypes;
  private _point: RatingLikeEnum = RatingLikeEnum.point;
  private _tooltip: string[];
  private _tooltipPlacement: TooltipPlacement = 'top';
  private _size: 'extra-small' | 'small' | 'medium' | 'large' = 'small';
  private _disabledList: number[] = [];

  constructor(private ts: AdaptTranslateService,
              _adaptRadarService: AdaptRadarService,
              _elem: ElementRef<HTMLElement>) {
    super(_adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptRatingLike);

    this.setDefaultTexts(ts.getCurrentLanguage());
  }

  setDefaultTexts(translation: KeyValueObject): void {
    this.tooltip = [
      translation['adapt.rating.like.l1'],
      translation['adapt.rating.like.l2'],
      translation['adapt.rating.like.l3'],
      translation['adapt.rating.like.l4'],
      translation['adapt.rating.like.l5'],
    ];
  }

  handleClick(e: MouseEvent, index: number): void {
    if (!this.disabled) {
      this._stopDisabled(e, index);
      this.selectedLike = index;
      this.onChange.emit({type: this.ratingValues[index]});
    }
  }

  private _stopDisabled(e: Event, index: number): void {
    if (this.disabledList.indexOf(index) === -1) {
      e.preventDefault();
      e.stopPropagation();
    }
  }
}
